.body-root{
    background-color: white;
    color:black;
    width:100%;
    min-height:70vh;
}

.body-content{
    display:flex;
    flex-wrap: wrap;
    height:100%;
    width:50%;
    min-width: 15cm;
    max-width: 20cm;
    margin: auto;
    padding-left: 2%;
    padding-right: 2%;
}

.break {
    flex-basis: 100%;
    height: 0;
}

